import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'app-wrods',
  templateUrl: './wrods.component.html',
  styleUrls: ['./wrods.component.css']
})
export class WrodsComponent implements OnInit {
  fitterstatus = 'xemtatca';
  isForm = false;
  ischeck = false;
  newEn = '';
  newVn = '';
  arrWords = [
    {id: 1, en: 'action', vn: 'hành động', memorized: true},
    {id: 2, en: 'actor', vn: 'diễn viên', memorized: false},
    {id: 3, en: 'activity', vn: 'hoạt động', memorized: true},
    {id: 4, en: 'active', vn: 'chủ động', memorized: true},
    {id: 5, en: 'bath', vn: 'tắm', memorized: false},
    {id: 6, en: 'bedroom', vn: 'phòng ngủ', memorized: true}
  ];

  constructor() {
  }

  ngOnInit(): void {
  }
  // them
  // tslint:disable-next-line:typedef
  funtion_add(){
    this.arrWords.unshift({
      id : this.arrWords.length + 1,
      en : this.newEn,
      vn : this.newVn,
      memorized: this.ischeck
    });
    this.newEn = '';
    this.newVn = '' ;
    this.isForm = false;
  }
  // xoa
  // tslint:disable-next-line:typedef
  funtion_remove(id: number){
   const index =   this.arrWords.findIndex(e => e.id === id);
   this.arrWords.splice(index, 1 );
  }

  // fifter mang
  // tslint:disable-next-line:typedef
  funtion_show(memorized: boolean){
    const xemall = this.fitterstatus === 'xemtatca';
    const xemdanho = this.fitterstatus === 'xemdanho' && memorized;
    const xemchuanho = this.fitterstatus === 'xemchuanho' && !memorized;
    return xemall || xemdanho || xemchuanho ;
  }

}
