import {Component, OnInit} from '@angular/core';
import {Form, FormControl, FormGroup, FormBuilder,Validators} from '@angular/forms';

@Component({
  selector: 'app-reactive-form',
  templateUrl: './reactive-form.component.html',
  styleUrls: ['./reactive-form.component.css']
})
export class ReactiveFormComponent implements OnInit {

  mFormGroup: FormGroup

  constructor(private mfb: FormBuilder) {
  }

  ngOnInit(): void {
    this.mFormGroup = this.mfb.group({
      email: ["",[checkGmail,Validators.email]],
      pass: '',
      subject: this.mfb.group({
        nodejs: false,
        Angular: true,
        PHP: false
      })
    })
  }

  onSubmit() {
    console.log("Xuất : ", this.mFormGroup.value)
  }

}
function checkGmail(mFormControl : FormControl) {
  if(mFormControl.value.includes('@gmail.com')){
    return null
  }
  return { gmail : true }

}
