import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'roundPipe'
})
export class RoundPipePipe implements PipeTransform {

  transform(value: number, isround: boolean, value1: number): number {
    if (isround) {
      return Math.ceil(value + value1);
    }
    return Math.floor(value + value1);
  }

}
